export function useCaptcha() {
  const [challenge, setChallenge] = useState<CaptchaChallenge | null>(null)
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null)
  const [isCorrect, setIsCorrect] = useState<boolean | null>(null)
  const [attempts, setAttempts] = useState(0)
  const [isVerified, setIsVerified] = useState(false)

  // Generate random challenge
  const generateChallenge = () => {
    const randomIndex = Math.floor(Math.random() * challenges.length)
    setChallenge(challenges[randomIndex])
    setSelectedAnswer(null)
    setIsCorrect(null)
  }

  // Initialize with random challenge
  useEffect(() => {
    // Check if already verified in session
    const verified = sessionStorage.getItem('captcha_verified')
    if (verified === 'true') {
      setIsVerified(true)
    } else {
      generateChallenge()
    }
  }, [])

  // Verify answer
  const verifyAnswer = () => {
    if (selectedAnswer === null || !challenge) return

    const correct = selectedAnswer === challenge.correctAnswer
    setIsCorrect(correct)
    setAttempts((prev) => prev + 1)

    if (correct) {
      setIsVerified(true)
      sessionStorage.setItem('captcha_verified', 'true')
      
      // Redirect after short delay
      setTimeout(() => {
        window.location.href = '/'
      }, 1500)
    } else {
      // Generate new challenge after wrong answer
      setTimeout(() => {
        generateChallenge()
      }, 1500)
    }
  }

  // Select answer
  const selectAnswer = (index: number) => {
    if (isCorrect !== null) return // Prevent changing after verification
    setSelectedAnswer(index)
  }

  // Reset captcha
  const reset = () => {
    generateChallenge()
    setAttempts(0)
    setIsVerified(false)
    sessionStorage.removeItem('captcha_verified')
  }

  return {
    challenge,
    selectedAnswer,
    isCorrect,
    attempts,
    isVerified,
    selectAnswer,
    verifyAnswer,
    reset,
  }
}
